body {
  color: #484848; }

body, input, select, textarea {
  font-size: 17px;
  font-family: "Fira Sans", Helvetica, sans-serif;
  font-weight: 400;
  color: #484848; }

a {
  font-weight: 500;
  color: #484848;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.25s ease-in-out; }
  a:hover, a:focus {
    color: #1fc0fc; }
  a:active {
    color: #027099; }

strong, b {
  font-weight: 700;
  color: #484848; }

em, i {
  font-style: italic; }

h1, h2, h3, h4, h5, h6 {
  color: #1fc0fc;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase; }
  h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: inherit;
    text-decoration: none; }

h1 {
  font-family: "Raleway", Helvetica, sans-serif;
  font-size: 200px;
  line-height: 1.70; }

h2 {
  font-size: 40px; }

h3 {
  font-size: 24px; }

h4 {
  font-size: 21px; }

.main-header {
  padding-top: 1px;
  min-height: 879px;
  background: url("../img/main-header__girl.png") center 0px no-repeat, url("../img/main-header__israel.png") center 35% no-repeat, url("../img/main-header__bg.png") center 0px no-repeat;
  background-size: 1366px 906px, 1018px 340px, 1366px 879px; }

.main-header__wrapper {
  margin: 0 auto;
  margin-top: 30px;
  padding: 0 15px;
  width: 1172px; }

.main-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px; }

.main-header__order-call {
  border-bottom: 1px dashed; }

.main-header__promo {
  position: relative;
  margin: 0 auto;
  margin-top: 455px;
  width: 350px; }
  .main-header__promo p {
    font-size: 24px;
    font-weight: 700;
    color: #ffffff;
    text-align: center; }

.main-header__scroll-down {
  position: absolute;
  left: 140px;
  bottom: -110px;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 76px;
  width: 76px;
  margin: 0 auto;
  background-color: transparent;
  border: 1px dashed #ffffff;
  border-radius: 50%;
  cursor: pointer; }
  .main-header__scroll-down img {
    width: 17px;
    height: 26px; }
  .main-header__scroll-down span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 54px;
    height: 54px;
    border: 1px solid #ffffff;
    border-radius: 50%; }
  .main-header__scroll-down:after {
    content: '';
    position: absolute;
    bottom: 0;
    margin-bottom: -55px;
    width: 1px;
    height: 55px;
    background-color: #ffffff; }

@media (max-width: 1200px) {
  .main-header__wrapper {
    width: auto; } }

@media (max-width: 767px) {
  .main-header {
    background-position: center 85px, center 25%, center 30px;
    background-size: 950px auto, 110% auto, 950px 679px; }
  .main-header__wrapper {
    padding: 0 10px; }
  .main-header__top {
    flex-direction: column;
    align-items: flex-end;
    font-size: 14px; }
    .main-header__top a {
      margin-bottom: 15px; }
    .main-header__top a:last-child {
      margin-bottom: 0; }
  .main-header__logo {
    position: absolute;
    left: 10px; }
  .main-header__promo {
    margin-top: 320px;
    width: 300px; }
    .main-header__promo p {
      font-size: 20px; }
  .main-header__scroll-down {
    bottom: -110px; } }
